<template>
    <div class="clearfix">
        <goods-card v-for="item in list" :key="item.productId" :image="item.image" :product-name="item.productName" :price="item.price"
            :category-name="item.categoryName" :origin="item.origin"></goods-card>
    </div>
</template>

<script>
import GoodsCard from './GoodsCard.vue'
export default {
    components: {
        GoodsCard
    },
    data() {
        return {
            addGoodsVisible: false,//是否显示添加页面
            currentPage: 1,//当前页码
            total: 0,//列表数量总数
            pageSize: 5,//当前页码的数量
            keyword: null,//搜索内容
            categoryId: null,//分类显示的分类ID
            list: [],//列表数据
        }
    },
    created: function () {
        //发送请求，请求商品第一页
        this.requestGoodsData({})
    },
    methods: {
        // 请求当前页码的数据
        handleCurrentChange(index) {
            this.requestGoodsData({ currentPage: index })
        },
        // 搜索
        searchGoods() {
            let keyword = this.keyword
            this.requestGoodsData({ keyword })
        },
        // 分类显示
        showCategoryGoods() {
            let categoryId = this.categoryId
            this.requestGoodsData({ categoryId })
        },
        // 显示所有
        showAllGoods() {
            // 将分类显示的分类ID = null
            this.categoryId = null
            // 清空搜索内容
            this.keyword = ''
            this.requestGoodsData({})
        },
        // 添加成功时，刷新数据
        onAddSuccess(msg) {
            this.requestGoodsData({}, msg)
        },
        // 请求
        requestGoodsData({ currentPage = 1, pageSize = this.pageSize, keyword = null, categoryId = null }, successMsg = '成功加载数据') {
            // console.log(currentPage, pageSize, keyword, categoryId)
            let params = {
                currentPage,
                pageSize,
                keyword,
                categoryId
            }
            this.$api.goods.get(params)
                .then((response) => {
                    console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success(successMsg)
                        let { list, total } = res.data
                        this.list = list
                        this.total = total
                    } else {
                        this.$message.error(response.data.msg)
                    }
                })
        },
    }


}
</script>

