<template>
    <div class="login_container">
        <div class="login_form">
            <h2>LOGIN</h2>
            <div class="login_form_input">
                <input type="text" v-model="username" placeholder="请输入用户名">
            </div>
            <div class="login_form_input">
                <input type="password" v-model="password" placeholder="请输入密码">
            </div>
            <button @click="onLogin">登录</button><br>
            <div class="login_form_footer">
                未有帐号？
                <span @click="register = true">注册</span>
            </div>
        </div>
        <!-- 注册 -->
        <Register v-if="register" @close="register = false"></Register>
    </div>
</template>

<script>
import Register from './Register.vue';

export default {
    name: 'MyLogin',
    components: { Register },
    data() {
        return {
            register: false,
            username: '',
            password: '',
            email: '',
            phone: ''
        }
    },
    methods: {
        onLoginClick() {
            if (this.username === 'admin' && this.password === '123456') {
                this.$message({
                    message: '成功登陆',
                    type: 'success'
                })
                this.$router.push('/adminHome')
                // return localStorage.setItem('token1', 'ruan')
                //保存当前登陆用户的ID
                localStorage.setItem('loginID', 7647676144)
                //保存当前登陆用户的用户名
                localStorage.setItem('loginName', "测试")
                this.$router.push('/userHome')
            }
            localStorage.removeItem('token')
        },
        async onLogin() {
            this.$api.login.login(this.username, this.password)
                .then(function (response) {
                    console.log(response);
                    const res = response.data
                    //状态200时
                    if (res.code === 200) {
                        this.$message({
                            message: '成功登陆',
                            type: 'success'
                        })
                        //token验证
                        // return localStorage.setItem('token1', 'ruan')
                        //保存当前登陆用户的ID
                        localStorage.setItem('loginID', res.data.id)
                        //保存当前登陆用户的用户名
                        localStorage.setItem('loginName', res.data.username)
                        if (res.data.role === 1) {
                            this.$router.push('/adminHome')
                        } else {
                            this.$router.push('/userHome')
                        }

                    } else {
                        this.$message.error(response.data.msg)
                    }
                }.bind(this))
                .catch(function (error) {
                    console.log(error);
                    this.$message.error('出错了')
                }.bind(this));
        }
    }
}
</script>

