<template>
    <div class="userhome_header">
        <div class="header_logo ">
            <a class=" logo_img" href=""></a>
        </div>
        <div id="search" :class="{ search_fix: isFixed }">
            <div class="container">
                <div class="fix_logo"></div>
                <div class="header_search">
                    <input class="search_input" type="text" placeholder="请输入关键字搜索">
                    <input class="search_submit" type="submit" value="搜索">
                </div>
                <div class="header_cart">
                    <router-link :to="'/userInfo/' + loginID + '/cart'">
                        <div class="cart_container">
                            <div class="el-icon-shopping-cart-full"></div>
                            <span>购物车</span>
                        </div>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "UserhomeHeader",
    props: {
        loginID: {
            type: String,
            default: '-1'
        }
    },
    data() {
        return {
            isFixed: false
        }
    },
    mounted: function () {
        //监听滚动条位置，使其固定定位
        window.addEventListener('scroll', () => {
            let scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
            if (scrollTop > 100) {
                this.isFixed = true
            } else {
                this.isFixed = false
            }
        })
    }

}
</script>

<style scoped>
.userhome_header {
    width: 1190px;
    height: 100px;
    margin: 0 auto;
    padding-top: 23px;
    box-sizing: border-box;
    position: relative;
}

.header_logo {
    float: left;
    margin-left: 17px;
    margin-top: -14px;
}

.userhome_header .header_logo .logo_img {
    width: 150px;
    height: 90px;
    display: block;
    background-image: url('@/assets/img/logo.png');
    background-repeat: no-repeat;
    justify-content: middle;
}


/* 固定定位样式 */
.search_fix {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: 99999;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.search_fix .fix_logo {
    float: left;
    margin-left: 80px;
    margin-top: 11px;
    height: 41px;
    width: 71px;
    background-image: url('../../assets/img/logo.png');
    background-size: contain;
    background-repeat: no-repeat;

}

.userhome_header .container {
    width: 1190px;
    margin: 0 auto;
    position: relative;
}

.userhome_header .container .header_search {
    width: 660px;
    margin: 0 auto;
    padding: 8px 0;
}

.userhome_header .header_search .search_input {
    width: 450px;
    height: 18px;
    padding: 10px;
    padding-left: 40px;
    border: 2px solid #409eff9e;
    border-right: none;
    border-radius: 20px 0px 0px 20px;
    vertical-align: middle;
}

.userhome_header .header_search .search_input:hover {
    border-color: #409EFF;
}

.userhome_header .header_search .search_input:focus {
    border-color: #409EFF;
    outline: none;
}

.userhome_header .header_search .search_submit {
    color: white;
    font-size: 17px;
    font-weight: bold;
    height: 41px;
    padding: 0 20px;
    border: hidden;
    border-radius: 0px 20px 20px 0px;
    background-color: #409EFF;
    vertical-align: middle;
    cursor: pointer
}

.userhome_header .container .header_cart {
    height: 45px;
    width: 120px;
    position: absolute;
    top: 10px;
    right: 170px;
    cursor: pointer;
}

.userhome_header .container .header_cart a:hover {
    text-decoration: none;
}

.userhome_header .container .cart_container {
    border: 2px solid #409eff9e;
    border-radius: 40px;
    height: 35px;
    padding: 0 15px;
    line-height: 35px;

}

.userhome_header .container .cart_container:hover {
    background-color: #40a0ff10;
    color: #409EFF;

}



.userhome_header .container .cart_container span {
    font-size: 15px;
    vertical-align: middle;
}

.userhome_header .container .el-icon-shopping-cart-full {
    margin-right: 5px;
    color: #409EFF;
    font-size: 28px;
    font-weight: 700;
    vertical-align: middle;
}
</style>